.footer-contact { position: relative; box-sizing: border-box; color: #fff; background-color: #000; background-size: cover; background-position: center; text-align: center; padding: 3rem 0; min-height: 500px; display: flex; flex-direction: column; justify-content: center; }
.footer-contact::before { content: ''; position: absolute; width: 100%; height: 100%; top: 0; left: 0; background: rgb(0 0 0 / 75%); }
.footer-contact .container { z-index: 2; }
.footer-contact .container > *:last-child { margin-bottom: 0; }
.footer-contact [role="doc-subtitle"] { display: block; margin-bottom: 1rem; font-size: 0.875rem; }
.footer-contact h2 { font-family: 'Newsreader'; font-weight: 300; font-size: 4.5rem; margin: 0 auto 0.25em auto; max-width: 680px; line-height: 1.1; }

@media only screen and (max-width: 1399px) {
    .footer-contact { min-height: 400px; padding: 2.5rem 0; }
    .footer-contact h2 { font-size: 4rem; }
}

@media only screen and (max-width: 1199px) {
    .footer-contact { min-height: 350px; padding: 2.25rem 0; }
    .footer-contact h2 { font-size: 3.5rem; }
}

@media only screen and (max-width: 991px) {
    .footer-contact { min-height: 325px; padding: 2rem 0; }
    .footer-contact h2 { font-size: 3rem; }
    .footer-contact [role=doc-subtitle] { margin-bottom: 0.5rem; }
}

@media only screen and (max-width: 767px) {
    .footer-contact { min-height: 300px; padding: 1.5rem 0; }
    .footer-contact h2 { font-size: 2.5rem; }
}

@media only screen and (max-width: 575px) {
    .footer-contact { min-height: 250px; padding: 1.25rem 0; }
    .footer-contact h2 { font-size: 2rem; }
}